.main-page-container {
    a{
        text-decoration: none;
    }
  .wrapper {
    padding: 1rem 0 6rem 0;
    position: relative;
    z-index: 1;
  }
  .inner {
    margin: 0 auto;
    width: 75rem;
    max-width: calc(100% - 6rem);
  }
  header.special {
    text-align: center;
    margin-bottom: 4rem;
  }
  .testimonials {
    margin: 2.5rem 0;
    width: 100%;
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;

    -moz-align-items: -moz-stretch;
    -webkit-align-items: -webkit-stretch;
    -ms-align-items: -ms-stretch;
    align-items: stretch;
  }
  .testimonials .content {
    border-radius: 4px;
    height: 100%;
    padding: 3rem;
    background: #ffffff;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.025);
  }
  .blockquote {
    border-left: solid 0.5rem;
    font-style: italic;
    margin: 0 0 2rem 0;
    padding: 1rem 0 1rem 2rem;
    border-left-color: rgba(0, 0, 0, 0.25);
  }
  .goto-faq{
    font-weight: bolder;
    text-decoration: underline;
    cursor: pointer;
  }
  .consultation-section{
    .us-dv-header{
      font-size: 65px!important;
      font-family: serif;
      font-weight: bolder;
    }
    .text{
      font-size: 3rem;

    }
  } 
  .icon-container{
    image{
      height: 65px;
      width: 65px;
      border-radius: 28px;
    }
    
  }
}
@media only screen and (max-width: 900px) {
    .main-page-container {
        .wrapper{
            padding: 0;
        }
        .inner{
            width: 100%;
            max-width: unset;
            padding: 10px;
        }
        .testimonials {
            display: block;
            .content{
                padding: 0.1rem;
            }
        }
        .blockquote{
            padding: 5px;
            border-left: 0;
        }
        .about-us-container{
            .text-container{
                height: auto;
            }
        }
        .home-container{
            .about-us-container{
                height: auto;
            }
        }

    }

}